import React from "react";
import "./AdminDashboard.css";
import Tick from "../../assets/svgs/Tick";
import ArrowDown from "../../assets/svgs/ArrowDown";
import { useState, useRef, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import axios from "axios";
import { toast } from "react-hot-toast";
import {
  createAnime,
  updateAnime,
  deleteAnimeThunk,
  createAnimeSeason,
} from "../../redux/webSlice";

const AdminDashboard = () => {
  const navigate = useNavigate();
  const { Token, socket, animes, user, error, success, loading } = useSelector(
    (state) => ({
      ...state.web,
    })
  );
  const { id } = useParams();
  let currentAnime = animes?.find((anime) => anime._id === id);
  const dispatch = useDispatch();

  const [animeSeasons, addAnimeSeasons] = useState({
    season: 1,
    playerOne: "",
    playerTwo: "",
    playerThree: "",
  });

  const [seasonChooser, setSeasonChooser] = useState(1);

  const handleSeasons = (e) => {
    const { value, name } = e.target;
    addAnimeSeasons({ ...animeSeasons, [name]: value });
  };

  const [players, updatePlayers] = useState({
    playerOne: "",
    playerTwo: "",
    playerThree: "",
    seasonId: "",
    playerId: "",
  });

  const handlePlayers = (e) => {
    const { value, name } = e.target;
    updatePlayers({ ...players, [name]: value });
  };

  const handleCreateAnimeSeason = (e) => {
    if (animeSeasons.playerOne === "") {
      return toast.error(" ", {
        id: "single",
        duration: 4000,
        style: {
          backgroundColor: "black",
          border: "1px solid #D084E3",
          color: "white",
          boxShadow: "0px 0px 30px #D084E3",
        },
      });
    }
    dispatch(createAnimeSeason({ Token, animeSeasons, animeId: id }));
  };

  let [animeGenres, setGenres] = useState([
    { value: "_", state: false },
    { value: "", state: false },
    { value: "_", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "-", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "-", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
    { value: "", state: false },
  ]);

  const [animeData, setAnimeData] = useState({
    name: "",
    year: "",
    voiceover: "",
    translator: "",
    totaltime: "",
    genres: [],
    director: "",
    studio: "",
    age: "",
    description: "",
    background: "",
    series: "",
    uploadDate: "",
    status: "",
  });

  useEffect(() => {
    if (id !== "new") {
      let anime = animes?.find((anime) => anime._id === id);
      if (anime !== undefined) {
        const {
          name,
          year,
          voiceover,
          translator,
          totaltime,
          genres,
          director,
          studio,
          age,
          description,
          background,
          series,
          uploadDate,
          status,
        } = anime;
        animeGenres.map((animeGenre) => {
          if (genres.includes(animeGenre.value)) {
            animeGenre.state = true;
          }
        });
        setAnimeData({
          ...animeData,
          name,
          year,
          voiceover,
          translator,
          totaltime,
          director,
          studio,
          age,
          description,
          background,
          series,
          uploadDate,
          status,
        });
      }
    }
  }, [id, animes]);

  const imageRef = useRef(null);
  const [playerOptions, setPlayerOptions] = useState({
    playerTwo: false,
    playerThree: false,
  });
  const [genresDropdown, triggerGenresDropdown] = useState(false);
  const [statusDropdown, triggerStatusDropdown] = useState(false);

  const changeAvatar = async (e) => {
    e.preventDefault();
    try {
      const file = e.target.files[0];
      let formData = new FormData();
      formData.append("file", file);
      let loader = true;
      loader &&
        toast.loading("  ...", {
          id: "single",
          duration: 4000,
          style: {
            backgroundColor: "black",
            border: "1px solid #D084E3",
            color: "white",
            boxShadow: "0px 0px 30px #D084E3",
          },
        });
      const { data } = await axios.post(
        "/api/user/uploadAnimeImage",
        formData,
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            Authorization: `Bearer ${Token}`,
          },
        }
      );
      loader = false;
      setAnimeData({ ...animeData, background: data.payload });
      toast.success(data.message, {
        id: "single",
        duration: 4000,
        style: {
          backgroundColor: "black",
          border: "1px solid #D084E3",
          color: "white",
          boxShadow: "0px 0px 30px #D084E3",
        },
      });
    } catch (error) {
      toast.error(error.response.data.message, {
        id: "single",
        duration: 4000,
        style: {
          backgroundColor: "black",
          border: "1px solid #D084E3",
          color: "white",
          boxShadow: "0px 0px 30px #D084E3",
        },
      });
    }
  };

  const changeAnimeData = (e) => {
    const { name, value } = e.target;
    setAnimeData({ ...animeData, [name]: value });
  };

  const addAnime = () => {
    const {
      name,
      year,
      voiceover,
      translator,
      totaltime,
      director,
      studio,
      age,
      description,
      background,
      series,
      uploadDate,
      status,
    } = animeData;
    let genresArray = [];
    animeGenres.map((genre) => {
      if (genre.state) genresArray.push(genre.value);
    });

    dispatch(
      createAnime({
        payload: {
          name,
          year,
          voiceover,
          translator,
          totaltime: parseInt(totaltime),
          director,
          studio,
          age: parseInt(age),
          description,
          background,
          genres: genresArray,
          series,
          uploadDate,
          status,
        },
        Token,
      })
    );
  };

  const handleUpdateAnime = async () => {
    const {
      name,
      year,
      voiceover,
      translator,
      totaltime,
      director,
      studio,
      age,
      description,
      background,
      series,
      uploadDate,
      status,
    } = animeData;
    let genresArray = [];
    animeGenres.map((genre) => {
      if (genre.state) genresArray.push(genre.value);
    });
    await dispatch(
      updateAnime({
        payload: {
          name,
          year,
          voiceover,
          translator,
          totaltime: parseInt(totaltime),
          director,
          studio,
          age: parseInt(age),
          description,
          background,
          genres: genresArray,
          series,
          uploadDate,
          status,
        },
        Token,
        animeId: id,
      })
    );
  };
  useEffect(() => {
    if (
      success !== null &&
      user?.role === 1 &&
      success !== "  "
    ) {
      console.log("ho");
      return navigate("/admin");
    }
  }, [success]);
  return (
    <main id="admin_dashboard">
      <aside id="top_admin">
        <aside id="top_left_admin">
          <div
            id="admin_dashboard_background"
            style={{ backgroundImage: `url(${animeData?.background})` }}
          ></div>
          <article>
            <button onClick={() => imageRef.current.click()}>
               
            </button>
            <input
              ref={imageRef}
              style={{ display: "none" }}
              onChange={changeAvatar}
              type="file"
              accept="image/png, image/jpeg, image/webp"
            />
            {id !== "new" && (
              <section id="admin_dashboard_players">
                <div>
                  <span> 2</span>
                  <div
                    onClick={() =>
                      setPlayerOptions({
                        ...playerOptions,
                        playerTwo: !playerOptions.playerTwo,
                      })
                    }
                    className={
                      playerOptions.playerTwo ? "trigger_player" : undefined
                    }
                  >
                    <Tick />
                  </div>
                </div>
                <div>
                  <span> 3</span>
                  <div
                    onClick={() =>
                      setPlayerOptions({
                        ...playerOptions,
                        playerThree: !playerOptions.playerThree,
                      })
                    }
                    className={
                      playerOptions.playerThree ? "trigger_player" : undefined
                    }
                  >
                    <Tick />
                  </div>
                </div>
              </section>
            )}
          </article>
        </aside>
        <aside id="top_right_admin">
          <section id="admin_dashboard_information">
            <ul>
              <li>
                <input
                  type="text"
                  placeholder=""
                  name="name"
                  value={animeData.name}
                  onChange={changeAnimeData}
                  autoComplete="off"
                />
                <div className="input_border"></div>
              </li>
              <li>
                <input
                  type="text"
                  placeholder=""
                  name="year"
                  value={animeData.year}
                  onChange={changeAnimeData}
                  autoComplete="off"
                />
                <div className="input_border"></div>
              </li>
              <li>
                <input
                  type="text"
                  placeholder=""
                  name="voiceover"
                  value={animeData.voiceover}
                  onChange={changeAnimeData}
                  autoComplete="off"
                />
                <div className="input_border"></div>
              </li>
              <li>
                <input
                  type="text"
                  placeholder=""
                  name="translator"
                  value={animeData.translator}
                  onChange={changeAnimeData}
                  autoComplete="off"
                />
                <div className="input_border"></div>
              </li>
              <li>
                <input
                  type="text"
                  placeholder=""
                  name="uploadDate"
                  value={animeData.uploadDate}
                  onChange={changeAnimeData}
                  autoComplete="off"
                />
                <div className="input_border"></div>
              </li>
              <li>
                <input
                  type="text"
                  placeholder=""
                  name="totaltime"
                  value={animeData.totaltime}
                  onChange={(e) => {
                    const re = /^[0-9]*$/;
                    if (e.target.value === "" || re.test(e.target.value)) {
                      changeAnimeData(e);
                    }
                  }}
                  autoComplete="off"
                />
                <div className="input_border"></div>
              </li>
            </ul>
            <ul>
              <li>
                <div
                  className={`admin_dropdown_header ${
                    genresDropdown
                      ? "triggered_gernes_header_dropdown"
                      : undefined
                  }`}
                  onClick={() => triggerGenresDropdown(!genresDropdown)}
                >
                  <span></span>
                  <ArrowDown />
                </div>
                <div
                  id="anime_genres_dropdown"
                  className={
                    genresDropdown ? "triggered_gernes_dropdown" : undefined
                  }
                >
                  <ul>
                    {animeGenres.map((genre, index) => (
                      <li
                        key={index}
                        onClick={() =>
                          setGenres((prevGenres) => {
                            let newState = [...prevGenres];
                            newState[index].state = !newState[index].state;
                            return newState;
                          })
                        }
                      >
                        <div
                          className={genre.state ? "modified_genre" : undefined}
                        >
                          <Tick />
                        </div>
                        <span>{genre.value}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              </li>
              <li>
                <input
                  type="text"
                  placeholder=""
                  name="director"
                  value={animeData.director}
                  onChange={changeAnimeData}
                  autoComplete="off"
                />
                <div className="input_border"></div>
              </li>
              <li>
                <input
                  type="text"
                  placeholder=""
                  name="studio"
                  value={animeData.studio}
                  onChange={changeAnimeData}
                  autoComplete="off"
                />
                <div className="input_border"></div>
              </li>
              <li>
                <input
                  type="text"
                  placeholder=""
                  name="age"
                  pattern="[0-9]"
                  value={animeData.age}
                  onChange={(e) => {
                    const re = /^[0-9]*$/;
                    if (e.target.value === "" || re.test(e.target.value)) {
                      changeAnimeData(e);
                    }
                  }}
                  autoComplete="off"
                />
                <div className="input_border"></div>
              </li>
              <li>
                <input
                  type="text"
                  placeholder=" "
                  name="series"
                  value={animeData.series}
                  onChange={(e) => {
                    const re = /^[0-9]*$/;
                    if (e.target.value === "" || re.test(e.target.value)) {
                      changeAnimeData(e);
                    }
                  }}
                  autoComplete="off"
                />
                <div className="input_border"></div>
              </li>
              <li>
                <div
                  className={`admin_dropdown_header ${
                    statusDropdown
                      ? "triggered_gernes_header_dropdown"
                      : undefined
                  }`}
                  onClick={() => triggerStatusDropdown(!statusDropdown)}
                >
                  <span></span>
                  <ArrowDown />
                </div>
                <div
                  id="anime_genres_dropdown"
                  className={
                    statusDropdown ? "triggered_gernes_dropdown" : undefined
                  }
                >
                  <ul>
                    <li
                      onClick={() =>
                        setAnimeData({ ...animeData, status: "" })
                      }
                    >
                      <div
                        className={
                          animeData?.status === ""
                            ? "modified_genre"
                            : undefined
                        }
                      >
                        <Tick />
                      </div>
                      <span></span>
                    </li>
                    <li
                      onClick={() =>
                        setAnimeData({ ...animeData, status: "" })
                      }
                    >
                      <div
                        className={
                          animeData?.status === ""
                            ? "modified_genre"
                            : undefined
                        }
                      >
                        <Tick />
                      </div>
                      <span></span>
                    </li>
                  </ul>
                </div>
              </li>
            </ul>
          </section>
          <textarea
            placeholder=""
            name="description"
            value={animeData.description}
            onChange={changeAnimeData}
            autoComplete="off"
          />
          <div>
            {" "}
            {id !== "new" && (
              <button
                className="deleteAnime"
                onClick={() =>
                  dispatch(deleteAnimeThunk({ payload: id, Token }))
                }
              >
                
              </button>
            )}
            <button
              onClick={() => {
                if (id === "new") {
                  addAnime();
                } else {
                  handleUpdateAnime();
                }
              }}
            >
              {id === "new" ? "" : ""}
            </button>
          </div>
        </aside>
      </aside>
      {id !== "new" && (
        <aside id="bottom_admin">
          <div className="admin_input_wrapper">
            <input
              name="playerOne"
              value={animeSeasons.playerOne}
              onChange={handleSeasons}
              type="text"
              placeholder=" 1"
            />
            <div></div>
          </div>
          {playerOptions.playerTwo && (
            <div className="admin_input_wrapper">
              <input
                name="playerTwo"
                value={animeSeasons.playerTwo}
                onChange={handleSeasons}
                type="text"
                placeholder=" 2"
              />
              <div></div>
            </div>
          )}
          {playerOptions.playerThree && (
            <div className="admin_input_wrapper">
              <input
                name="playerThree"
                value={animeSeasons.playerThree}
                onChange={handleSeasons}
                type="text"
                placeholder=" 3"
              />
              <div></div>
            </div>
          )}
          <div id="season_wrapper">
            <div
              onClick={() => {
                if (animeSeasons.season !== 1)
                  addAnimeSeasons((prevState) => ({
                    ...prevState,
                    season: prevState.season - 1,
                  }));
              }}
            >
              <ArrowDown />
            </div>
            <span> {animeSeasons.season}</span>
            <div
              onClick={() =>
                addAnimeSeasons((prevState) => ({
                  ...prevState,
                  season: prevState.season + 1,
                }))
              }
            >
              <ArrowDown />
            </div>
          </div>
          <button onClick={() => handleCreateAnimeSeason()}>
             
          </button>

          <section id="admin_series_information">
            <aside>
              <h1></h1>
              <div id="admin_seasones_switch">
                <div
                  onClick={() => {
                    if (seasonChooser !== 1) {
                      setSeasonChooser((prevChooser) => prevChooser - 1);
                    }
                  }}
                >
                  <ArrowDown />
                </div>
                <span> {seasonChooser}</span>
                <div
                  onClick={() => {
                    if (seasonChooser !== currentAnime?.seasons?.length) {
                      setSeasonChooser((prevChooser) => prevChooser + 1);
                    }
                  }}
                >
                  <ArrowDown />
                </div>
              </div>
              <ul>
                {currentAnime?.seasons?.map((output) => {
                  if (output?.index === seasonChooser) {
                    return output?.series.map((outputTwo, index) => (
                      <li
                        onClick={() => {
                          updatePlayers({
                            playerOne: outputTwo.playerOne,
                            playerTwo: outputTwo.playerTwo,
                            playerThree: outputTwo.playerThree,
                            seasonId: output._id,
                            playerId: outputTwo._id,
                          });
                        }}
                        key={index}
                      >
                         {index + 1}
                      </li>
                    ));
                  }
                })}
              </ul>
            </aside>
            <aside>
              <h1></h1>
              <div id="admin_season_parameters">
                <div>
                  <label htmlFor=""> 1</label>
                  <input
                    type="text"
                    value={players.playerOne}
                    name="playerOne"
                    onChange={handlePlayers}
                  />
                </div>
                <div>
                  <label htmlFor=""> 2</label>
                  <input
                    type="text"
                    value={players.playerTwo}
                    name="playerTwo"
                    onChange={handlePlayers}
                  />
                </div>
                <div>
                  <label htmlFor=""> 3</label>
                  <input
                    type="text"
                    value={players.playerThree}
                    name="playerThree"
                    onChange={handlePlayers}
                  />
                </div>
                <div id="admin_season_button_wrapper">
                  <button
                    onClick={() => {
                      if (players?.seasonId === "") {
                        return toast.error("  ", {
                          id: "single",
                          duration: 4000,
                          style: {
                            backgroundColor: "black",
                            border: "1px solid #D084E3",
                            color: "white",
                            boxShadow: "0px 0px 30px #D084E3",
                          },
                        });
                      }
                      toast.loading("  ...", {
                        id: "single",
                        duration: 4000,
                        style: {
                          backgroundColor: "black",
                          border: "1px solid #D084E3",
                          color: "white",
                          boxShadow: "0px 0px 30px #D084E3",
                        },
                      });
                      let currentSeason = currentAnime?.seasons.find(
                        (output) => output._id === players.seasonId
                      );
                      if (currentSeason?.series.length === 1) {
                        return toast.error("    ", {
                          id: "single",
                          duration: 4000,
                          style: {
                            backgroundColor: "black",
                            border: "1px solid #D084E3",
                            color: "white",
                            boxShadow: "0px 0px 30px #D084E3",
                          },
                        });
                      }
                      socket.emit("deleteSeries", {
                        Token,
                        data: {
                          seasonId: players?.seasonId,
                          playerId: players?.playerId,
                        },
                      });
                      updatePlayers({
                        playerOne: "",
                        playerTwo: "",
                        playerThree: "",
                        seasonId: "",
                        playerId: "",
                      });
                    }}
                  >
                    
                  </button>
                  <button
                    onClick={() => {
                      if (players?.seasonId === "") {
                        return toast.error("  ", {
                          id: "single",
                          duration: 4000,
                          style: {
                            backgroundColor: "black",
                            border: "1px solid #D084E3",
                            color: "white",
                            boxShadow: "0px 0px 30px #D084E3",
                          },
                        });
                      }
                      socket.emit("updateSeries", { Token, data: players });
                      return toast.success("  ", {
                        id: "single",
                        duration: 4000,
                        style: {
                          backgroundColor: "black",
                          border: "1px solid #D084E3",
                          color: "white",
                          boxShadow: "0px 0px 30px #D084E3",
                        },
                      });
                    }}
                  >
                    
                  </button>
                </div>
              </div>
            </aside>
          </section>
          {/* <article className="ADFirst"> </article>
          <article className="ADSecond"> </article> */}
        </aside>
      )}
    </main>
  );
};

export default AdminDashboard;
